.app-header {
  width: 10rem;
  height: 1.17333rem;
  overflow: hidden;
  background-color: #fff;
  line-height: 1.17333rem;
  text-align: center;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  border-bottom: 0.02667rem solid #e2e3e4; }
  .app-header .app-header-title {
    font-size: 0.48rem;
    color: #1f2427; }
  .app-header .back-btn {
    position: absolute;
    left: 0;
    top: 0;
    width: 1.06667rem;
    height: 1.17333rem;
    background: url(./image/i-left.png) 0.4rem 0.36rem no-repeat;
    background-size: 0.25333rem 0.44rem; }
  .app-header .add-btn {
    position: absolute;
    right: 0;
    top: 0;
    width: 1.06667rem;
    height: 1.17333rem;
    background: url(./image/i-add.png) 0.24rem 0.38667rem no-repeat;
    background-size: 0.42667rem 0.42667rem; }
  .app-header .edit-btn {
    position: absolute;
    right: 0;
    top: 0;
    width: 1.6rem;
    height: 1.17333rem;
    text-align: center;
    line-height: 1.17333rem;
    color: #000;
    font-size: 0.4rem; }

.no-border {
  border-bottom: none; }



